// Colors
$colors: (main: #000, accent: #ffc32a, alt: #4c4c4c);

// Base params
$cover-font-size: 6rem;
$cover-border: 1.6rem;
$cover-padding: 1.8rem;

// Responsive params
$cover-font-size-lg: $cover-font-size - 2rem * 0.5;
$cover-font-size-md: $cover-font-size - 2rem * 1;
$cover-font-size-sm: $cover-font-size - 2rem * 1;
$cover-font-size-xs: $cover-font-size - 2rem * 1.5;

$cover-border-lg: $cover-border - 0.4rem * 1.5;
$cover-border-md: $cover-border - 0.4rem * 2;
$cover-border-sm: $cover-border - 0.4rem * 2;
$cover-border-xs: $cover-border - 0.4rem * 2.5;

$cover-padding-lg: $cover-padding - 0.5rem;
$cover-padding-md: $cover-padding - 1rem;
$cover-padding-sm: $cover-padding - 1rem;
$cover-padding-xs: $cover-padding - 1.2rem;



// About

// Base params
$about-font: 5rem;
$about-margin: 10rem;

$about-margin-md: $about-margin - $about-margin * 0.3;
$about-margin-xs: $about-margin - $about-margin * 0.6;

$about-font-sm: $about-font - $about-font * 0.3;
$about-font-xs: $about-font - $about-font * 0.6;
