// _________________ Section _________________
#landing {
  background: url("../images/landing_background.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;

  &, .row {
    height: 100vh; } }


// _________________ Cover Name _________________
.cover-name {
  padding: $cover-padding ($cover-padding * 3);
  font-size: $cover-font-size;
  text-transform: uppercase;
  color: map-get($colors, accent);
  border: $cover-border solid map-get($colors, accent); }


// _________________ Scroll Icon _________________
.scroll-icon {
  color: map-get($colors, accent);
  position: absolute;
  width: 6rem;
  text-align: center;
  top: calc(100vh - 14rem);

  span {
    font-size: 1.1rem;
    letter-spacing: 0.2rem; }

  svg {
    * {
      transform-origin: center; }

    rect, line {
      fill: none;
      stroke: map-get($colors, accent);
      stroke-miterlimit: 10;
      stroke-width: 18px;
      stroke-linecap: round; }

    line {
      animation: ScrollAnimation 1s ease-in-out infinite; }

    circle {
      fill: map-get($colors, accent);
      stroke-width: 0;
      animation: TapScrollAnimation 2s ease-in-out infinite; } } }
