// _______________ Landing Page _______________
@media screen and (max-width: 1200px) {
  .cover-name {
    font-size: $cover-font-size-lg;
    border-width: $cover-border-lg;
    padding: $cover-padding-lg ($cover-padding-lg * 3); } }

@media screen and (max-width: 992px) {
  .cover-name {
    font-size: $cover-font-size-md;
    border-width: $cover-border-md;
    padding: $cover-padding-md ($cover-padding-md * 3); } }

@media screen and (max-width: 768px) {
  .cover-name {
    font-size: $cover-font-size-sm;
    border-width: $cover-border-sm;
    padding: $cover-padding-sm ($cover-padding-sm * 3); } }

@media screen and (max-width: 576px) {
  .cover-name {
    font-size: $cover-font-size-xs;
    border-width: $cover-border-xs;
    padding: $cover-padding-xs ($cover-padding-xs * 3); } }


// _______________ About Page _______________
// @media screen and (max-width: 1200px)


@media screen and (max-width: 992px) {
  #about {
    h1 {
      margin-top: $about-margin-md; } } }

@media screen and (max-width: 768px) {
  #about {
    h1 {
      font-size: $about-font-sm; }
    p {
      font-size: $about-font-sm * 0.6; } } }

@media screen and (max-width: 576px) {
  #about {
    background: map-get($colors, accent);
    h1 {
      margin-top: $about-margin-xs; }
    p {
      font-size: $about-font-xs * 0.95; } } }

@media screen and (max-width: 360px) {
  #about {
    h1 {
      margin-top: $about-margin-xs * 0.8; }
    p {
      font-size: $about-font-xs * 0.8; } } }
