// Mouse scroll animation from landing page
@keyframes ScrollAnimation {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(15px); }
  100% {
    transform: translateY(0); } }

// Tap scrolling animation for mobile
@keyframes TapScrollAnimation {
    10% {
      transform: scale(1); }
    20% {
      transform: scale(1.5); }
    30% {
      transform: scale(1.3) translateY(0); }
    70% {
      opacity: 1; }
    100% {
      transform: scale(1.3) translateY(-120px);
      opacity: 0; } }
